<script>
  import Display from "./Display.svelte";
  import Control1 from "./Control1.svelte";
  import Control2 from "./Control2.svelte";

  let childCount1 = 3;
  let flexDirection1 = "column";
  let justifyContent1 = "flex-start";
  let alignItems1 = "center";

  let withParent2 = false;
  let childCount2 = 2;
  let flexDirection2 = "row";
  let justifyContent2 = "flex-start";
  let alignItems2 = "center";

  function changeChildCount1(v) {
    childCount1 = Number(v);
  }
  function changeFlexDirection1(v) {
    flexDirection1 = v;
  }
  function changeJustifyContent1(v) {
    justifyContent1 = v;
  }
  function changeAlignItems1(v) {
    alignItems1 = v;
  }

  function changeWithParent2(v) {
    withParent2 = v;
  }

  function changeChildCount2(v) {
    childCount2 = Number(v);
  }
  function changeFlexDirection2(v) {
    flexDirection2 = v;
  }
  function changeJustifyContent2(v) {
    justifyContent2 = v;
  }
  function changeAlignItems2(v) {
    alignItems2 = v;
  }

  let printOutputAll = "";
  let printOutput1 = "";
  let printOutput2 = "";
  $: {
    printOutput1 = "";
    printOutput2 = "";
    // OUTPUT2
    if (flexDirection2.startsWith("column")) {
      printOutput2 += "Column(";
    }
    if (flexDirection2 === "column-reverse") {
      printOutput2 +=
        "\n                          verticalDirection: VerticalDirection.up,";
    }
    if (flexDirection2.startsWith("row")) {
      printOutput2 += "Row(";
    }
    if (flexDirection2 === "row-reverse") {
      printOutput2 +=
        "\n                          textDirection: TextDirection.rtl,";
    }

    if (justifyContent2 === "flex-end") {
      printOutput2 +=
        "\n                          mainAxisAlignment: MainAxisAlignment.end,";
    }
    if (justifyContent2 === "center") {
      printOutput2 +=
        "\n                          mainAxisAlignment: MainAxisAlignment.center,";
    }
    if (justifyContent2 === "space-around") {
      printOutput2 +=
        "\n                          mainAxisAlignment: MainAxisAlignment.spaceAround,";
    }
    if (justifyContent2 === "space-between") {
      printOutput2 +=
        "\n                          mainAxisAlignment: MainAxisAlignment.spaceBetween,";
    }
    if (alignItems2 === "stretch") {
      printOutput2 +=
        "\n                          crossAxisAlignment: CrossAxisAlignment.stretch,";
    }
    if (alignItems2 === "baseline") {
      printOutput2 +=
        "\n                          crossAxisAlignment: CrossAxisAlignment.baseline,";
    }
    if (alignItems2 === "flex-start") {
      printOutput2 +=
        "\n                          crossAxisAlignment: CrossAxisAlignment.start,";
    }
    if (alignItems2 === "flex-end") {
      printOutput2 +=
        "\n                          crossAxisAlignment: CrossAxisAlignment.end,";
    }
    printOutput2 += "\n                          children: <Widget>[";
    for (let index = 1; index <= childCount2; index++) {
      printOutput2 += "\n                                    Container(";
      printOutput2 += `\n                                     color: Colors.teal,`;
      printOutput2 += `\n                                     child: Text("${index}"),`;
      printOutput2 += "\n                                    ),";
    }
    printOutput2 += "\n                                   ],";
    printOutput2 += "\n                         ),";

    // OUTPUT 1
    if (flexDirection1.startsWith("column")) {
      printOutput1 += "Column(";
    }
    if (flexDirection1 === "column-reverse") {
      printOutput1 += "\n       verticalDirection: VerticalDirection.up,";
    }
    if (flexDirection1.startsWith("row")) {
      printOutput1 += "Row(";
    }
    if (flexDirection1 === "row-reverse") {
      printOutput1 += "\n       textDirection: TextDirection.rtl,";
    }

    if (justifyContent1 === "flex-end") {
      printOutput1 += "\n       mainAxisAlignment: MainAxisAlignment.end,";
    }
    if (justifyContent1 === "center") {
      printOutput1 += "\n       mainAxisAlignment: MainAxisAlignment.center,";
    }
    if (justifyContent1 === "space-around") {
      printOutput1 +=
        "\n       mainAxisAlignment: MainAxisAlignment.spaceAround,";
    }
    if (justifyContent1 === "space-between") {
      printOutput1 +=
        "\n       mainAxisAlignment: MainAxisAlignment.spaceBetween,";
    }

    if (alignItems1 === "stretch") {
      printOutput1 +=
        "\n       crossAxisAlignment: CrossAxisAlignment.stretch,";
    }
    if (alignItems1 === "baseline") {
      printOutput1 +=
        "\n       crossAxisAlignment: CrossAxisAlignment.baseline,";
    }
    if (alignItems1 === "flex-start") {
      printOutput1 += "\n       crossAxisAlignment: CrossAxisAlignment.start,";
    }
    if (alignItems1 === "flex-end") {
      printOutput1 += "\n       crossAxisAlignment: CrossAxisAlignment.end,";
    }
    printOutput1 += "\n       children:<Widget>[";

    for (let index = 1; index <= childCount1; index++) {
      if (!withParent2 || childCount2 < 1) {
        printOutput1 += "\n                 Container(";
        printOutput1 += `\n                  color: Colors.blue,`;
        printOutput1 += `\n                  child: Text("${index}"),`;
        printOutput1 += "\n                 ),";
      } else {
        printOutput1 += "\n                 Container(";
        printOutput1 += `\n                  color: Colors.blue,`;
        printOutput1 += `\n                  child: ` + printOutput2;
        printOutput1 += "\n                 ),";
      }
    }

    printOutput1 += "\n                ],";
    printOutput1 += "\n       ),";

    printOutputAll = "";
    printOutputAll += "Container(";
    printOutputAll += "\n color: Colors.white,";
    printOutputAll += "\n width: double.infinity,";
    printOutputAll += "\n height: double.infinity,";
    printOutputAll += "\n margin: EdgeInsets.all(0),";
    printOutputAll += "\n padding: EdgeInsets.fromLTRB(0, 0, 0, 0),";
    printOutputAll += "\n child: " + printOutput1;
    printOutputAll += "\n)";
  }
</script>

<style>
  .flex-responsive {
    display: flex;
  }
  @media only screen and (max-width: 872px) {
    .flex-responsive {
      display: block;
      padding-left: 4px;
    }
  }

  .preview-container {
    min-width: 550px;
    margin: 0px 20px;
  }
</style>

<div class="page-heading">Web flexbox with flutter output</div>
<div class="flex-responsive">
  <Display
    {childCount1}
    {flexDirection1}
    {justifyContent1}
    {alignItems1}
    {withParent2}
    {childCount2}
    {flexDirection2}
    {justifyContent2}
    {alignItems2} />
  <Control1
    {flexDirection1}
    {justifyContent1}
    {alignItems1}
    {changeChildCount1}
    {changeFlexDirection1}
    {changeJustifyContent1}
    {changeAlignItems1}
    {withParent2}
    {changeWithParent2} />
  {#if withParent2}
    <Control2
      {flexDirection2}
      {justifyContent2}
      {alignItems2}
      {changeChildCount2}
      {changeFlexDirection2}
      {changeJustifyContent2}
      {changeAlignItems2} />
  {/if}
</div>

<div class="preview-container">
  <div class="control-heading">FULL FLUTTER WIDGET OUTPUT</div>

  <pre class="preview">{printOutputAll}</pre>

</div>
